import React from 'react';
import {
     Switch,
     Route
   } from "react-router-dom";
   import Scroll from '../Scroll';
   import {SectionWrapper,SectionHeading,SectionLinks,LinkText,LinkItemWrapper,Section,LinkItem} from '../Styles/SectionStyles';
   import Option1 from './Option1';
   import Option2 from './Option2';
   import Option3 from './Option3';
   import Option4 from './Option4';
   import Option5 from './Option5';
   import Option6 from './Option6';

const Section3 = () => {
  


  return (
    <SectionWrapper>
      <Section>
        <SectionHeading>🧮Анализ финансовых результатов</SectionHeading>
        <SectionLinks>

          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option1">
              <LinkText onClick={Scroll}>EBIT</LinkText>
            </LinkItem>
          </LinkItemWrapper>
          
          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option2">
              <LinkText onClick={Scroll}>EBITDA</LinkText>
            </LinkItem>
          </LinkItemWrapper>
          
          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option3">
              <LinkText onClick={Scroll}>EBITDARM</LinkText>
            </LinkItem>
          </LinkItemWrapper>

          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option4">
              <LinkText onClick={Scroll}>Норма чистой прибыли</LinkText>
            </LinkItem>
          </LinkItemWrapper>
          
          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option5">
              <LinkText onClick={Scroll}>Рентабельность</LinkText>
            </LinkItem>
          </LinkItemWrapper>

          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section3/option6">
              <LinkText onClick={Scroll}>Рентабельность активов</LinkText>
            </LinkItem>
          </LinkItemWrapper>
        
        </SectionLinks>
     </Section>
      <Switch>
        <Route path="/finance-analytics-app/section3/option6"> 
          <Option6/>
        </Route>
        <Route path="/finance-analytics-app/section3/option5"> 
          <Option5/>
        </Route>
        <Route path="/finance-analytics-app/section3/option4"> 
          <Option4/>
        </Route>
        <Route path="/finance-analytics-app/section3/option3"> 
          <Option3/>
        </Route>
        <Route path="/finance-analytics-app/section3/option2"> 
          <Option2/>
        </Route>
        <Route path="/finance-analytics-app/section3/option1"> 
          <Option1/>
        </Route>
      </Switch>
    </SectionWrapper>
  );
} 

export default Section3;
