import React from 'react';
import {
     Switch,
     Route
   } from "react-router-dom";
   import {SectionWrapper,SectionHeading,SectionLinks,LinkText,LinkItemWrapper,Section,LinkItem} from '../Styles/SectionStyles';
   import Option1 from './Option1';

const Section4 = () => {

  return (
    <SectionWrapper>
      <Section>
        <SectionHeading>🧮Анализ оборачиваемости и основных средств</SectionHeading>
        <SectionLinks>

          <LinkItemWrapper>
            <LinkItem to="/finance-analytics-app/section4/option1">
              <LinkText>Денежный цикл (цикл оборотного капитала)</LinkText>
            </LinkItem>
          </LinkItemWrapper>
        
        </SectionLinks>
     </Section>
      <Switch>
        
        <Route path="/finance-analytics-app/section4/option1"> 
          <Option1/>
        </Route>
      </Switch>
    </SectionWrapper>
  );
} 

export default Section4;
