import styled,{ createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
     *{
          box-sizing: border-box;
          font-family: 'Helvetica', sans-serif;
          border: none;
          outline: none;
          margin: 0;
          padding: 0;
          color: black;
          scroll-behavior: smooth;
     }    
     body {
          min-height: 100vh;
     }
     li{
          list-style-type: none;
     }
`;
